} 

.dd__menu li {
  padding: 8px 15px;
  cursor: pointer;
  white-space: nowrap;
  background-color: #837F7F;
  border-radius: 10px;
}

.dd__menu li:hover {
  background-color: #928d8d;
}

.dd__menu li a {
