}

.dd__pictures {
  display: flex;
  flex-direction: column;
  &-price {
    display: block;
    width: 20px;
    height: 20px;

    background-image: url(../../img/group-by-price.svg);
    background-repeat: no-repeat;
    background-size: contain;
  }

  &-date {
    display: block;
    width: 20px;
    height: 20px;

    background-image: url(../../img/filter-by-date.svg);
    background-repeat: no-repeat;
    background-size: contain;
  }
}

